export default '現在、通商や交流の点で自国外と関連のある紙幣、切手などには「NIPPON」と描かれ（紙幣発券者も「にっぽんぎんこう」である）ているほか、NHK、日本テレビ[10]、ニッポン放送、日本武道館、全日本空輸、近畿日本鉄道、西日本鉄道、日本体育大学、日本郵便、NEXCO東日本・NEXCO中日本・NEXCO西日本[11]、日本電気、日本電信電話、日本郵船、日本通運、NTT東日本[10]・NTT西日本[10]などで「NIPPON」（にっぽん）表記を用いる一方、「NIHON」（にほん）表記を用いる例は、日本大学、日本航空、日本経済新聞、日本たばこ産業、JR東日本・JR西日本、日本ユニシス、日本相撲協会、日本交通、日本オリンピック委員会、NTT東日本、などがある。日本経済新聞が2016年に行った調査によると、社名に「日本」が含まれる上場企業の読み方は、「にほん」が60%、「にっぽん」が40%であり、「にっぽん」と読ませる企業の比率が増加傾向にあった。テレビ番組名では「にっぽん」が使われることが多くなってきている[12]。なお、日本国憲法の読みについて、内閣法制局は、読み方について特に規定がなく、どちらでもよいとしている[13]。憲法制定の際、読みについての議論で担当の金森徳次郎国務大臣は「ニホン、ニッポン両様の読み方がともに使われることは、通念として認められている」と述べており、どちらかにはされなかった[9]。';
